.page__header {
    box-sizing: border-box;
    margin-top: var(--space-l-xl);
    margin-bottom: var(--space-xl-4xl); 
}


  
.logoside {
    display: grid;
    grid-template-columns: min-content 1fr;
    grid-column-gap:var(--space-xs);
    column-gap: var(--space-xs);
    line-height: 1;
    align-items: center;
}
 

.navbar-logo {
    background-image: url(/assets/images/logo/bartoshevich@2x.webp);
    background-image: image-set(
      url(/assets/images/logo/bartoshevich@1x.avif) type("image/avif") 1x,
      url(/assets/images/logo/bartoshevich@2x.avif) type("image/avif") 2x,
      url(/assets/images/logo/bartoshevich@1x.webp) type("image/webp") 1x,
      url(/assets/images/logo/bartoshevich@2x.webp) type("image/webp") 2x,
      url(/assets/images/logo/bartoshevich@1x.jpg) type("image/jpeg") 1x,
      url(/assets/images/logo/bartoshevich@2x.jpg) type("image/jpeg") 2x
    );
  border-radius: 50%;
  width: var(--space-xl-2xl);
  height: var(--space-xl-2xl);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; 
}



.logo__title {
    margin: 0 0 .2em 0;
    font-weight: bold;  
    font-size: var(--step-0); 
}

.menu__button {
        z-index: 1;
        display: block;
        margin: 0;
        cursor: pointer;
        border: none;
        background-color: transparent;        
    }
.menu__button:focus-visible {
    outline: 3px solid $link-active;
}



.menu__icon {
    display: block;
    stroke: $text-colour;
}

.menu__icon:hover {
    stroke: $text-colour;
}

.menu__icon-menu,
.menu__button--open .menu__icon-cross {
    display: block;
}

.menu__icon-cross,
.menu__button--open .menu__icon-menu {
    display: none;
}


.menu-header__wrapper  {        
        padding: 1em 0;
    }



.menu__header {
        display: none;
        transition: all 5s;
    }


@media (max-width:639px) {
.menu__header--open {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        overflow: hidden;   
        transition: all 5s; 
    }

  
.menu__header__item {
    display: grid;
    grid-template-columns: var(--space-xl-2xl) 1fr;
    grid-template-rows: 1em 1fr;
    grid-gap: 12px;
}


.item__title {
    grid-column: 1/-1;
    grid-row:1;
}

.item__content {
    grid-column: 2;
    grid-row:2;
}
}




@media (min-width:640px) {
    .menu__header--open {
        display: grid;
        grid-template-columns: 2fr 4fr 4fr;   
        grid-gap: 2rem;
        overflow: hidden;   
        transition: all 5s;   
    }
}


@media (min-width:640px) {
    .menu__header__item {
        display: grid;
        grid-template-columns: 1fr 3fr;
        grid-gap: 1em;
    }
}


.header__item--bleed {
    grid-column: 2;
}

@media (min-width:640px) {
    .header__item--bleed {
        grid-column: 1/-1;
    }
}



.menu--flex {
        display: flex;
        flex-direction: row;
        justify-content: space-between;   
   
    }

 

    .nav__group {
        margin: 0;
        padding: 0;
        list-style: none;
        
      }
    .nav__li {
        margin: .4em 0 .65em 0;
    }  

    .nav__li--selected {
        box-shadow: none;
        background-color: var(--selected-bg-color);
    	border-radius: 8px;
        text-decoration: none;
	    color: $text-colour; 
    }

